<template>
  <el-container>
    <el-main>
      <el-card>教育经历</el-card>

      <el-card>工作经历</el-card>

      <el-card>项目经验</el-card>

      <el-card>
        <el-button v-on:click="click">加载信息</el-button>
      </el-card>
      <el-card>
        <el-button v-on:click="getResume">查询RESYUME</el-button>
      </el-card>
      <el-card>{{resume}}</el-card>

      <router-link to="/test">跳转</router-link>
    </el-main>
  </el-container>
</template>

<style scoped>
.el-main {
  padding: 10px;
}
</style>

<script>
export default {
  name: "Detail",
  data() {
    return {
      resume: null,
      ressume1: null
    };
  },

  methods: {
    click() {
      this.$http.get("/api/resume/1").then(res => {
        this.resume = res.data;
        console.log(this.resume);
        return (resume = this.resume);
      });
    },
    getResume() {
      this.$http.get("/api/getresume?id=1").then(res => {
        this.ressume1 = res.data.results;
        console.log(this.ressume1);
        return (resume1 = this.ressume1);
      });
    }
  }
};
</script>



