<template >
  <div >
    <el-button @click="getResume">获取resume信息</el-button>
    <el-card>
    <div>{{info.name}}</div>
    <div>{{info.phone}}</div>
    <div>{{info.email}}</div>
    <div>{{info.blog}}</div>
    <div>{{info.website}}</div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "Test",
  data() {
    return {
      info:{
        id:null,
        name:null,
        phone:null,
        email:null,
        blog:null,
        website:null,
      }

    };
  },
  created(){
    this.getResume();

  },
  methods:{
    // getResume(){
    //   this.$http.get('/api/resume/1')
    //   .then(response => (this.info = response.data))

    // },
    getResume(){
      this.$http.get('/api/getresume',{
        params:{
          id:1
        }
      }).then(res => (this.info = res.data.results[0]))
    },

  },

};
</script>

<style scoped>
</style>


